import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Box,
  Button,
  TextField,
  Typography,
  Paper,
  Grid,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import DeleteIcon from '@mui/icons-material/Delete';

const OrderForm = () => {
  const navigate = useNavigate();
  const [pizzas, setPizzas] = useState([]);
  const [orderItems, setOrderItems] = useState([]);
  const [formData, setFormData] = useState({
    customerName: '',
    customerPhone: '',
    deliveryAddress: '',
  });

  useEffect(() => {
    fetchPizzas();
  }, []);

  const fetchPizzas = async () => {
    try {
      const response = await axios.get('http://localhost:8080/api/pizzas/available');
      setPizzas(response.data);
    } catch (error) {
      console.error('   :', error);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAddItem = () => {
    setOrderItems(prev => [...prev, {
      pizza: null,
      quantity: 1,
      price: 0
    }]);
  };

  const handleRemoveItem = (index) => {
    setOrderItems(prev => prev.filter((_, i) => i !== index));
  };

  const handleItemChange = (index, field, value) => {
    setOrderItems(prev => {
      const newItems = [...prev];
      if (field === 'pizza') {
        const pizza = pizzas.find(p => p.id === value);
        newItems[index] = {
          ...newItems[index],
          pizza: pizza,
          price: pizza ? pizza.basePrice : 0
        };
      } else if (field === 'quantity') {
        newItems[index] = {
          ...newItems[index],
          quantity: value,
          price: newItems[index].pizza ? newItems[index].pizza.basePrice * value : 0
        };
      }
      return newItems;
    });
  };

  const calculateTotal = () => {
    return orderItems.reduce((total, item) => total + item.price, 0);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const order = {
        ...formData,
        orderItems: orderItems.map(item => ({
          pizza: { id: item.pizza.id },
          quantity: item.quantity
        }))
      };
      await axios.post('http://localhost:8080/api/orders', order);
      navigate('/orders');
    } catch (error) {
      console.error('  :', error);
    }
  };

  return (
    <Box component="form" onSubmit={handleSubmit} sx={{ maxWidth: 800, mx: 'auto', p: 3 }}>
      <Typography variant="h4" gutterBottom> </Typography>
      
      <Paper sx={{ p: 3, mb: 3 }}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              label=""
              name="customerName"
              value={formData.customerName}
              onChange={handleInputChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              label=""
              name="customerPhone"
              value={formData.customerPhone}
              onChange={handleInputChange}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              required
              fullWidth
              label=""
              name="deliveryAddress"
              value={formData.deliveryAddress}
              onChange={handleInputChange}
            />
          </Grid>
        </Grid>
      </Paper>

      <Paper sx={{ p: 3, mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h6"> </Typography>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={handleAddItem}
          >
             
          </Button>
        </Box>

        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {orderItems.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>
                    <FormControl fullWidth>
                      <Select
                        value={item.pizza?.id || ''}
                        onChange={(e) => handleItemChange(index, 'pizza', e.target.value)}
                      >
                        {pizzas.map((pizza) => (
                          <MenuItem key={pizza.id} value={pizza.id}>
                            {pizza.name} ({pizza.basePrice.toLocaleString()})
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                  </TableCell>
                  <TableCell>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <IconButton
                        onClick={() => handleItemChange(index, 'quantity', Math.max(1, item.quantity - 1))}
                      >
                        <RemoveIcon />
                      </IconButton>
                      <TextField
                        type="number"
                        value={item.quantity}
                        onChange={(e) => handleItemChange(index, 'quantity', parseInt(e.target.value) || 1)}
                        inputProps={{ min: 1 }}
                        sx={{ width: 60, mx: 1 }}
                      />
                      <IconButton
                        onClick={() => handleItemChange(index, 'quantity', item.quantity + 1)}
                      >
                        <AddIcon />
                      </IconButton>
                    </Box>
                  </TableCell>
                  <TableCell>{item.price.toLocaleString()}</TableCell>
                  <TableCell>
                    <IconButton
                      color="error"
                      onClick={() => handleRemoveItem(index)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>

      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h6">
          : {calculateTotal().toLocaleString()}
        </Typography>
        <Box>
          <Button
            variant="outlined"
            onClick={() => navigate('/orders')}
            sx={{ mr: 2 }}
          >
            
          </Button>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            disabled={orderItems.length === 0}
          >
            
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default OrderForm; 