import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Typography,
  Chip,
  Box,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';

const OrderList = () => {
  const navigate = useNavigate();
  const [orders, setOrders] = useState([]);
  const [selectedStatus, setSelectedStatus] = useState('ALL');

  useEffect(() => {
    fetchOrders();
  }, [selectedStatus]);

  const fetchOrders = async () => {
    try {
      const response = selectedStatus === 'ALL'
        ? await axios.get('http://localhost:8080/api/orders')
        : await axios.get(`http://localhost:8080/api/orders/status/${selectedStatus}`);
      setOrders(response.data);
    } catch (error) {
      console.error('   :', error);
    }
  };

  const handleStatusChange = async (orderId, newStatus) => {
    try {
      await axios.put(`http://localhost:8080/api/orders/${orderId}/status?status=${newStatus}`);
      fetchOrders();
    } catch (error) {
      console.error('   :', error);
    }
  };

  const handleCancel = async (orderId) => {
    if (window.confirm('   ?')) {
      try {
        await axios.post(`http://localhost:8080/api/orders/${orderId}/cancel`);
        fetchOrders();
      } catch (error) {
        console.error('  :', error);
      }
    }
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'PENDING': return 'warning';
      case 'CONFIRMED': return 'info';
      case 'PREPARING': return 'primary';
      case 'READY': return 'success';
      case 'DELIVERING': return 'secondary';
      case 'COMPLETED': return 'success';
      case 'CANCELLED': return 'error';
      default: return 'default';
    }
  };

  const getStatusLabel = (status) => {
    switch (status) {
      case 'PENDING': return ' ';
      case 'CONFIRMED': return ' ';
      case 'PREPARING': return ' ';
      case 'READY': return ' ';
      case 'DELIVERING': return ' ';
      case 'COMPLETED': return '';
      case 'CANCELLED': return '';
      default: return status;
    }
  };

  return (
    <div>
      <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h4"> </Typography>
        <Button
          variant="contained"
          color="primary"
          onClick={() => navigate('/orders/new')}
        >
           
        </Button>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Button
          variant={selectedStatus === 'ALL' ? 'contained' : 'outlined'}
          onClick={() => setSelectedStatus('ALL')}
          sx={{ mr: 1 }}
        >
          
        </Button>
        {['PENDING', 'CONFIRMED', 'PREPARING', 'READY', 'DELIVERING', 'COMPLETED', 'CANCELLED'].map((status) => (
          <Button
            key={status}
            variant={selectedStatus === status ? 'contained' : 'outlined'}
            onClick={() => setSelectedStatus(status)}
            sx={{ mr: 1 }}
          >
            {getStatusLabel(status)}
          </Button>
        ))}
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell> </TableCell>
              <TableCell> </TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell> </TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {orders.map((order) => (
              <TableRow key={order.id}>
                <TableCell>{order.id}</TableCell>
                <TableCell>{new Date(order.orderDate).toLocaleString()}</TableCell>
                <TableCell>{order.customerName}</TableCell>
                <TableCell>{order.customerPhone}</TableCell>
                <TableCell>{order.deliveryAddress}</TableCell>
                <TableCell>
                  {order.orderItems.map((item) => (
                    <div key={item.id}>
                      {item.pizza.name} x {item.quantity}
                    </div>
                  ))}
                </TableCell>
                <TableCell>{order.totalAmount.toLocaleString()}</TableCell>
                <TableCell>
                  <Chip
                    label={getStatusLabel(order.status.statusName)}
                    color={getStatusColor(order.status.statusName)}
                  />
                </TableCell>
                <TableCell>
                  {order.status.statusName === 'PENDING' && (
                    <Button
                      variant="contained"
                      color="primary"
                      size="small"
                      onClick={() => handleStatusChange(order.id, 'CONFIRMED')}
                      sx={{ mr: 1 }}
                    >
                      
                    </Button>
                  )}
                  {order.status.statusName === 'CONFIRMED' && (
                    <Button
                      variant="contained"
                      color="primary"
                      size="small"
                      onClick={() => handleStatusChange(order.id, 'PREPARING')}
                      sx={{ mr: 1 }}
                    >
                       
                    </Button>
                  )}
                  {order.status.statusName === 'PREPARING' && (
                    <Button
                      variant="contained"
                      color="primary"
                      size="small"
                      onClick={() => handleStatusChange(order.id, 'READY')}
                      sx={{ mr: 1 }}
                    >
                       
                    </Button>
                  )}
                  {order.status.statusName === 'READY' && (
                    <Button
                      variant="contained"
                      color="primary"
                      size="small"
                      onClick={() => handleStatusChange(order.id, 'DELIVERING')}
                      sx={{ mr: 1 }}
                    >
                       
                    </Button>
                  )}
                  {order.status.statusName === 'DELIVERING' && (
                    <Button
                      variant="contained"
                      color="primary"
                      size="small"
                      onClick={() => handleStatusChange(order.id, 'COMPLETED')}
                      sx={{ mr: 1 }}
                    >
                       
                    </Button>
                  )}
                  {['PENDING', 'CONFIRMED', 'PREPARING'].includes(order.status.statusName) && (
                    <Button
                      variant="contained"
                      color="error"
                      size="small"
                      onClick={() => handleCancel(order.id)}
                    >
                      
                    </Button>
                  )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default OrderList; 