import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  IconButton,
  Typography,
} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';

const PizzaList = () => {
  const navigate = useNavigate();
  const [pizzas, setPizzas] = useState([]);

  useEffect(() => {
    fetchPizzas();
  }, []);

  const fetchPizzas = async () => {
    try {
      const response = await axios.get('http://localhost:8080/api/pizzas');
      setPizzas(response.data);
    } catch (error) {
      console.error('   :', error);
    }
  };

  const handleDelete = async (id) => {
    if (window.confirm('   ?')) {
      try {
        await axios.delete(`http://localhost:8080/api/pizzas/${id}`);
        setPizzas(pizzas.filter(pizza => pizza.id !== id));
      } catch (error) {
        console.error('  :', error);
      }
    }
  };

  const toggleAvailability = async (id) => {
    try {
      const response = await axios.patch(`http://localhost:8080/api/pizzas/${id}/availability`);
      setPizzas(pizzas.map(pizza => 
        pizza.id === id ? response.data : pizza
      ));
    } catch (error) {
      console.error('   :', error);
    }
  };

  return (
    <div>
      <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '20px' }}>
        <Typography variant="h4">  </Typography>
        <Button
          variant="contained"
          color="primary"
          startIcon={<AddIcon />}
          onClick={() => navigate('/pizzas/new')}
        >
            
        </Button>
      </div>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell align="right"></TableCell>
              <TableCell align="right"></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {pizzas.map((pizza) => (
              <TableRow key={pizza.id}>
                <TableCell>
                  <img 
                    src={pizza.imageUrl} 
                    alt={pizza.name} 
                    style={{ width: '50px', height: '50px', objectFit: 'cover' }}
                  />
                </TableCell>
                <TableCell>{pizza.name}</TableCell>
                <TableCell>{pizza.type}</TableCell>
                <TableCell>{pizza.size}</TableCell>
                <TableCell align="right">
                  {pizza.basePrice ? pizza.basePrice.toLocaleString() : '0'}
                </TableCell>
                <TableCell align="right">
                  {pizza.costPrice ? pizza.costPrice.toLocaleString() : '0'}
                </TableCell>
                <TableCell>{pizza.preparationTime}</TableCell>
                <TableCell>
                  <Button
                    variant={pizza.isAvailable ? "contained" : "outlined"}
                    color={pizza.isAvailable ? "success" : "error"}
                    size="small"
                    onClick={() => toggleAvailability(pizza.id)}
                  >
                    {pizza.isAvailable ? "" : ""}
                  </Button>
                </TableCell>
                <TableCell>
                  <IconButton 
                    color="primary"
                    onClick={() => navigate(`/pizzas/${pizza.id}/edit`)}
                  >
                    <EditIcon />
                  </IconButton>
                  <IconButton 
                    color="error"
                    onClick={() => handleDelete(pizza.id)}
                  >
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default PizzaList;